.nsr-settings-page .container > .nsr-switch {
	display: block;
	margin: 12px auto;
}

.nsr-switch {
	position: relative;
	display: inline-block;
	vertical-align: top;
	width: 40px;
	height: 16px;
	padding: 3px;
	margin-top: 6px;
	background-color: #fff;
	border-radius: 8px;
	box-shadow: inset 0 -1px #fff, inset 0 1px 1px rgba(0, 0, 0, 0.05);
	cursor: pointer;
	background-image: -webkit-linear-gradient(top, #eee, #fff 25px);
	background-image: -moz-linear-gradient(top, #eee, #fff 25px);
	background-image: -o-linear-gradient(top, #eee, #fff 25px);
	background-image: linear-gradient(to bottom, #eee, #fff 25px);
}

.nsr-switch-input {
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
}

.nsr-switch-label {
	position: relative;
	display: block;
	height: inherit;
	font-size: 8px;
	text-transform: uppercase;
	background: #eceeef;
	border-radius: inherit;
	box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.12), inset 0 0 2px rgba(0, 0, 0, 0.15);
	-webkit-transition: .15s ease-out;
	-moz-transition: .15s ease-out;
	-o-transition: .15s ease-out;
	transition: .15s ease-out;
	-webkit-transition-property: opacity;
	-moz-transition-property: opacity;
	-o-transition-property: opacity;
	transition-property: opacity;
}

.nsr-switch-label:after,
.nsr-switch-label:before {
	position: absolute;
	top: 50%;
	margin-top: -.5em;
	line-height: 1;
	-webkit-transition: inherit;
	-moz-transition: inherit;
	-o-transition: inherit;
	transition: inherit;
}

.nsr-switch-label:before {
	content: attr(data-off);
	font-size: 9px;
	top: 8px;
	right: 5px;
	color: #aaa;
	text-shadow: 0 1px rgba(255, 255, 255, 0.5);
}

.nsr-switch-label:after {
	content: attr(data-on);
	font-size: 9px;
	top: 8px;
	left: 8px;
	color: #fff;
	text-shadow: 0 1px rgba(0, 0, 0, 0.2);
	opacity: 0;
}

.nsr-switch-input:checked ~ .nsr-switch-label {
	background: #87d37c;
	box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.15), inset 0 0 3px rgba(0, 0, 0, 0.2);
}

.nsr-switch-input:checked ~ .nsr-switch-label:before {
	opacity: 0;
}

.nsr-switch-input:checked ~ .nsr-switch-label:after {
	opacity: 1;
}

.nsr-switch-handle {
	position: absolute;
	top: 4px;
	left: 4px;
	width: 14px;
	height: 14px;
	background: #fff;
	border-radius: 50%;
	box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
	background-image: -webkit-linear-gradient(top, #fff 40%, #f0f0f0);
	background-image: -moz-linear-gradient(top, #fff 40%, #f0f0f0);
	background-image: -o-linear-gradient(top, #fff 40%, #f0f0f0);
	background-image: linear-gradient(to bottom, #fff 40%, #f0f0f0);
	-webkit-transition: left .15s ease-out;
	-moz-transition: left .15s ease-out;
	-o-transition: left .15s ease-out;
	transition: left .15s ease-out;
}

.nsr-switch-handle:before {
	content: '';
	position: absolute;
	top: 50%;
	left: 50%;
	margin: -6px 0 0 -6px;
	width: 10px;
	height: 10px;
	background: #f9f9f9;
	border-radius: 5px;
	box-shadow: inset 0 1px rgba(0, 0, 0, 0.02);
	background-image: -webkit-linear-gradient(top, #eee, #fff);
	background-image: -moz-linear-gradient(top, #eee, #fff);
	background-image: -o-linear-gradient(top, #eee, #fff);
	background-image: linear-gradient(to bottom, #eee, #fff);
}

.nsr-switch-input:checked ~ .nsr-switch-handle {
	left: 30px;
	box-shadow: -1px 1px 5px rgba(0, 0, 0, 0.2);
}

.nsr-switch-green > .nsr-switch-input:checked ~ .nsr-switch-label {
	background: #4fb845;
}

#nsr_form .nsr-switch {
	pointer-events: initial;
}
