/************************************************************\
 *   Table
\************************************************************/
.nsr-settings-page .form-table {
	margin-top: 3px;
	background: #fff;
	border: 1px solid #dfdfdf;
}

#nsr_form table {
	width: inherit;
	table-layout: fixed;
	box-sizing: content-box;
}

#nsr_form tbody {
	width: 100%;
	min-width: 100%;
	display: table;
}

#nsr_form tr {
	box-sizing: border-box;
	display: table;
	width: 100%;
	height: 48px;
	clear: both;
	border-radius: 2px;
	border-bottom: 1px solid #f4f4f4;
}

.nsr-settings-page .form-table td > div,
.nsr-settings-page .form-table th {
	box-sizing: border-box;
	display: inline-block;
	position: relative;
	width: 200px;
}

#nsr_form .form-table th {
	float: left;
	box-sizing: border-box;
	height: auto;
	font-size: 12px;
	font-weight: 400;
	padding: 16px 0 10px 16px;
}

#nsr_form .form-table td {
	float: left;
	top: 25px;
	box-sizing: border-box;
	height: auto;
	font-size: 12px;
	font-weight: 400;
	padding: 4px 0 0 16px;
	margin-bottom: 0;
}

#nsr_form .form-table td > div {
	padding: 0;
	margin: 0;
	top: 0;
}
